header {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 100%;
    padding: 0% 5%;
    position: fixed;
    background: transparent;
    z-index: 1;
    font-family: 'Manrope';
}

hr{
    display: flex;
    align-self: center;    
    width: 5rem;
    height: 1px;
}

.menuList {
    display: inline-flex;
    width: 100%;
    justify-content: space-between;
    flex-direction: row;
    list-style-type: none;
    background: transparent;
}

.second {
    display: flex;
    gap: 48px;
    background: transparent;
}

.menuList li {
    color: white;
    cursor: pointer;
    background: transparent;
}


@media (max-width: 900px) {
    hr{
        display: none;
    }
}